import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { fetchEmployees } from "../../actions";

class EmployeeList extends React.Component {
  constructor(props) {
    super(props);
    this.state = { searchInput: "" };
    this.handleSearchInput = this.handleSearchInput.bind(this);
  }

  componentDidMount() {
    this.props.fetchEmployees();
  }

  handleSearchInput(e) {
    this.setState({ searchInput: e.target.value });
  }

  renderEmployee() {
    const searchRegex = new RegExp(`^${this.state.searchInput}`, "i");

    return this.props.employees
      .filter(employee => employee.email.match(searchRegex))
      .map(employee => {
        return (
          <tbody key={employee.id}>
            <tr>
              <td className="align-middle"> {employee.id} </td>
              <td className="align-middle"> {employee.name} </td>
              <td className="align-middle"> {employee.email} </td>
              <td className="align-middle"> {employee.company} </td>
              <td className="align-middle"> {employee.designation} </td>
              <td className="align-middle"> {employee.phone} </td>
              <td className="align-middle"> {employee.salary} </td>
              <td className="align-middle">
                <Link
                  className="btn btn-info d-flex justify-content-center mb-1"
                  to={`/employees/show/${employee.id}`}
                >
                  Show
                </Link>
                <Link
                  className="btn btn-success d-flex justify-content-center mb-1"
                  to={`/employees/edit/${employee.id}`}
                >
                  Update
                </Link>
                <Link
                  className="btn btn-danger d-flex justify-content-center"
                  to={`/employees/delete/${employee.id}`}
                >
                  Delete
                </Link>
              </td>
            </tr>
          </tbody>
        );
      });
  }

  render() {
    return (
      <div className="mb-5">
        <h1 className="text-center display-4 my-4">Employees</h1>
        <div className="form-row mb-3">
          <div className="col">
            <Link className="btn btn-primary mb-3" to="/employees/new">
              Add New Employee
            </Link>
          </div>
          <div className="col-4">
            <div className="input-group">
              <input
                type="text"
                className="form-control"
                placeholder="Type your email here..."
                value={this.state.searchInput}
                onChange={this.handleSearchInput}
              />
              <div className="input-group-append">
                <span className="input-group-text">
                  <i className="fas fa-search text-grey" />
                </span>
              </div>
            </div>
          </div>
        </div>
        <table className="table table-bordered table-striped">
          <thead className="thead-dark">
            <tr>
              <th>#</th>
              <th>Name</th>
              <th>Email</th>
              <th>Company</th>
              <th>Designation</th>
              <th>Phone</th>
              <th>Salary</th>
              <th>Actions</th>
            </tr>
          </thead>
          {this.renderEmployee()}
        </table>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return { employees: state.employees };
};

export default connect(
  mapStateToProps,
  { fetchEmployees }
)(EmployeeList);
